
// ==============================================

//        HERO

// ==============================================

.home-visu {
  display: block;
  position: relative;
  padding-top: 34%;
}

.home-visu__ratio {
  // aspect-ratio
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  background-image: url('../images/bg-home.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  .wrapper {
    position: relative;
    height: 100%;
    // @include bd(green,1,2);
  }
}

.home-visu__texts {
  padding-left: 100px;
  position: absolute;
  left: 0;
  top: 55%;
  transform: translateY(-50%);
}

.home-visu__title {
  position: relative;
}


.home-visu__title a {
  display: block;
  // font-size: calc( 16px + (66 - 16)*(100vw - 400px)/(1200 - 400));
  // font-size: calc(40px + 1.5vw);
  font-size: 66px;
  font-size: 5.5vw;
  font-weight: $weight-bold;
  line-height: 1.2;
  color: $color-primary;

  &:hover {
    .home-visu__icon-play {
      fill: $color-primary;
    }
  }

  // @media screen and (max-width: 400px){    font-size: 30px;    }
  @media screen and (min-width: 1200px){    font-size: 66px;    }
}

.home-visu__title span {
  color: white;
}

.home-visu__icon-play {
  height: 0.9em;
  width: 0.9em;
  margin-top: calc(1em / 8);
  fill: white;
  transform: translateY(5px);
  transition: fill 0.3s;
}

.home-visu__logo {
  position: absolute;
  top: -180px;
  left: -100px;
  width: 200px;
  height: 207px;
  transform-origin: bottom center;
}

@include breakpoint(large down) {
  .home-visu__logo {
    transform: scale(0.9);
  }
}
@include breakpoint(medium down) {
  .home-visu__logo {
    transform: scale(0.7);
  }
}
@include breakpoint(small only) {
  .home {
    margin-top: 60px;
  }

  .home-visu__logo {
    transform: scale(0.5);
  }

  .home-visu {
    padding-top: 0;
    height: 560px;
  }

  .home-visu__ratio {
    position: static;
    background-image: url('../images/bg-home-mobile.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    &:after {
      @include overlay();
      background-color: rgba(white, 0.7);
    }

    .wrapper {
      z-index: 1;
    }

    .home-visu__texts {
      padding-left: 60px;
    }

    .home-visu__title {
      width: 200px;
      // margin-right: 200px;
    }

    .home-visu__title a {
      font-size: 46px;

      br {
        display: none;
      }
    }
    .home-visu__title span {
      color: inherit;
    }

    .home-visu__icon-play {
      fill: $color-primary;
    }
  }
}



//  Red bar
// -----------------------------------------------

.hero-bar {
  background-color: $color-tertiary;

  .wrapper {
    padding: 2rem 2rem;
    text-align: center;
    background-image: url('../images/icons/picto-rdv.svg');
    background-position: 90% center;
    background-repeat: no-repeat;
  }

  .hero-bar__title {
    margin: 0 0 10px;
    font-size: 33px;
    font-weight: $weight-bold;
    line-height: 1.3;
    color: white;
  }
  .hero-bar__desc {
    margin: 0 0 1.5rem;
    color: white;
    font-size: 20px;
  }
}


@include breakpoint(medium down) {
  .hero-bar .wrapper {
    background-position: calc(100% + 70px) center;
  }
}
