
// ==============================================

//        FOOTER

// ==============================================


//  Row 1 - blue
// -----------------------------------------------

.footer1 {
  padding: 40px 0;
  font-size: 14px;
  color: white;
  background-color: $color-primary;
}


.footer__col {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-donconfiance {
  width: 84px;
  height: 84px;
  margin-right: 20px;
}

.btn-social + .btn-social {
  margin-left: 2rem;
}

.logo-icm-white {
  height: 100px;
  width: 86px;
  margin-left: 20px;
}


.footer__c3 {
  a {
    color: white;

    &:hover {
      text-decoration: underline;
    }
  }
}


@include breakpoint(large down) {
  .footer1 .wrapper {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@include breakpoint(medium down) {
  .footer__c1,
  .footer__c3 {
    flex-direction: column;
    text-align: center;
  }
  .logo-donconfiance {
    margin-right: 0;
    margin-bottom: 1.5rem;
  }
  .footer__c1-text,
  .footer__c3-text {
    max-width: 500px;
  }
  .footer__c2 {
    padding: 2rem 0;
  }
  .logo-icm-white {
    margin-left: 0;
    margin-top: 1.5rem;
  }
}


//  Row 2 - white
// -----------------------------------------------

.footer2 {
  padding: 50px 0;
  text-align: center;

  a {
    color: $color-body-text;

    &:hover {
      text-decoration: underline;
    }
  }
}
