
// ==============================================

//        NAV INTERVIEW

// ==============================================


//  Nav
// -----------------------------------------------

.nav-itw {
  height: 120px;
  margin-bottom: 60px;
  background-color: white;
}

.nav-itw__list {
  @include list;
  display: flex;
  height: 100%;
}

.nav-itw__item {
  display: flex;
  align-items: center;
  flex: 1 1 0;
  position: relative;
  height: 100%;

  &+ .nav-itw__item:after {
    content: '';
    position: absolute;
    left: 0;
    height: 40px;
    width: 2px;
    background-color: #DECCCA;
  }
}

.nav-itw__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 0 2rem;
  font-size: 34px;
  font-weight: $weight-bold;
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  color: $color-tertiary;
  transition: color 0.5s;

  &:hover {
    color: $color-primary;
  }

  &.is-active {
    color: $color-primary;

    .divider-shadow:before {
      box-shadow: 0 0 18px rgba(#002D5C, 0.8);
    }
  }

  .divider-shadow {
    position: absolute;
    left: 10%;
    bottom: -30px;
    width: 80%;
    pointer-events: none;

    @include divider-shadow($position:'top', $color:#9D7F7B, $alpha:0.5);
  }
}


@include breakpoint(medium down) {
  .nav-itw__link {
    font-size: 26px;
  }
}

@include breakpoint(small only) {
  .nav-itw {
    height: 230px;
  }

  .nav-itw__list {
    flex-direction: column;
  }

  .nav-itw__item {
    &+ .nav-itw__item:after {
      left: 50%;
      top: 0;
      height: 1px;
      width: 90%;
      transform: translateX(-50%);
    }
  }

  .nav-itw__link {
    .divider-shadow {
      display: none;
    }
  }
}
