
// ==============================================

//        SECTION FONDATION

// ==============================================

.fondation {
  padding: 80px 0;
  background-image: url('../images/bg-fondation.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  text-align: center;

  @include breakpoint(small only) {
    padding-bottom: 0;
  }
}



//  Bloc impot
// -----------------------------------------------

.fondation-impots {
  margin-bottom: 80px;

  @include breakpoint(large down) {
    margin-left: 1rem;
    margin-right: 1rem;
  }
}

.fondation-impots__title {
  margin-top: 0;
  margin-bottom: 50px;
  color: $color-primary;
}

.fondation-impot {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: 25px;
  max-width: 600px;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;

  &:nth-child(1) {
    --color-theme: #{$color-tertiary};
    --color-theme-bg: #FCF9F8;
    margin-right: 1rem;
  }
  &:nth-child(2) {
    --color-theme: #{$color-secondary};
    --color-theme-bg: #F0F7FA;
    margin-left: 1rem;
  }

  header {
    position: relative;
    flex-grow: 1;    // *** idem flex: 1 1 0 or flex: 1  ***
  }

  .accent {
    color: var(--color-theme);
  }
  .digit {
    padding: 0 70px;
    display: block;
    font-size: 60px;
    font-weight: $weight-bold;
    color: var(--color-theme);
  }

  .intro {
    margin: 0 0 1rem;
    padding: 0 70px;
    font-size: 20px;
    line-height: 1.3;
  }

  .example {
    position: relative;
    margin: 0;
    padding: 25px 70px 40px;
    font-size: 18px;
    line-height: 1.3;
    background-color: var(--color-theme-bg);

    .accent {
      font-size: 22px;
    }
  }

  .divider-shadow {
    position: absolute;
    left: 10%;
    bottom: 0;
    width: 80%;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: -13px;
    left: 50%;
    transform: translateX(-50%);
    height: 26px;
    width: 26px;
    border-radius: 50%;
    background-color: #eee;
    -webkit-mask-image: radial-gradient(100px at 50% 50% , transparent 95%, black 100%);
  }

  @include breakpoint(medium down) {
    margin-bottom: 1rem;

    &:nth-child(1) {
      margin-right: 0;
    }
    &:nth-child(2) {
      margin-left: 0;
    }
  }
}

.fondation-impot:nth-child(1) .divider-shadow {
  @include divider-shadow($position:'bottom', $color:#9D7F7B, $alpha:0.3);
}
.fondation-impot:nth-child(2) .divider-shadow {
  @include divider-shadow($position:'bottom', $color:#7B8C9D, $alpha:0.3);
}



//  Bloc axes
// -----------------------------------------------

.fondation-axes {
  padding: 60px 2rem 40px;
  text-align: center;
  background-color: rgba(white,0.75);
  border-radius: 10px;

  @include breakpoint(large down) {
    margin-left: 1rem;
    margin-right: 1rem;
  }

  @include breakpoint(small only) {
    margin-left: 0;
    margin-right: 0;
    background-color: white;
    border-radius: 0;
  }
}

.fondation-axes__title {
  margin-top: 0;
  margin-bottom: 50px;
  color: $color-tertiary;
}

.fondation-axe {
  text-align: center;
  padding: 0 2rem;

  .picto {
    height: 100px;
    width: 100px;
  }

  .fondation-axe__title {
    font-size: 27px;
    color: $color-tertiary;
    margin-bottom: 0;
  }

  p {
    margin-top: 5px;
  }

  @include breakpoint(large down) {
    padding: 0 5rem;
    margin-bottom: 2rem;
  }
  @include breakpoint(medium down) {
    padding: 0 2rem;
  }
}
