
// ==============================================

//        SECTION INTERVIEW

// ==============================================

.itw {
  background-color: #FCF9F8;
  padding-bottom: 80px;
}


.itw-slider {
  overflow: hidden;
}

.itw-slide {
  .itw-slide__inner {
    opacity: 0.2;
    transition: opacity 0.9s, transform 0.9s;
    // transform: translateX(-200px);
  }

  &.is-current {
    .itw-slide__inner {
      opacity: 1;
      // transform: translateX(0);
    }
  }
}

//  Video
// -----------------------------------------------


.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

.itw-video {
  height: 430px;
  background-color: #eeeeee;
}


//  Bloc texts
// -----------------------------------------------

.itw-bloc {
  padding: 0 20px;
}

.itw-bloc__title {
  margin-top: 1rem;
  font-size: 26px;
  line-height: 1.2;
  color: $color-primary;
}

.itw-bloc__subtitle {
  margin-bottom: 0;
  font-size: 22px;
  font-weight: $weight-bold;
  line-height: 1.3;
  color: $color-primary;

  span {
    color: $color-tertiary;
  }
}

.itw-bloc__text {
  ul {
    margin-top: 5px;
    padding-left: 30px;
  }
}

.itw-row1 {
  position: relative;
  margin-bottom: 30px;
}

.itw-divider-shadow {
  position: relative;

  @include divider-shadow($position:'top', $color:#9D7F7B, $alpha:0.5);
}


@include breakpoint(large down) {
  .itw-bloc {
    margin-bottom: 2rem;
  }
  .itw-divider-shadow {
    left: 10%;
    width: 80%;
  }
}
@include breakpoint(medium down) {
  .itw-bloc {
    text-align: center;
  }
  .itw-bloc__text {
    ul {
      list-style-type: none;
      padding-left: 0;
    }
  }
}



//  Testimonial
// -----------------------------------------------

.itw-testimonial {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-start;
  text-align: right;
}

.itw-testimonial__quote {
  position: relative;
  margin: 0;
  padding: 2rem 2rem;
  line-height: 1.4;
  font-size: 20px;

  &:before,
  &:after {
    content: '';
    position: absolute;
    z-index: -1;
    width: 50px;
    height: 40px;
  }

  &:before {
    top: 10px;
    left: 20px;
    background-image: url('../images/icons/picto-quote.svg');
  }

  &:after {
    bottom: 10px;
    right: 20px;
    background-image: url('../images/icons/picto-quote.svg');
    transform: scaleY(-1) scaleX(-1);
  }
}


.itw-testimonial__img {
  flex: 1 0 200px;
  padding: 2rem 0;
  text-align: center;

  img {
    display: inline-block;
    border-radius: 100%;
  }

  p {
    font-size: 22px;
    font-weight: $weight-bold;
    line-height: 1.3;
    color: $color-primary;
  }
}




//  Digits
// -----------------------------------------------

.itw-digits {
  position: relative;
  padding: 1.5rem 2rem;
  color: $color-primary;

  .divider-shadow {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 30px;
    overflow: hidden;
    // @include bd(red);

    &:before {
      content: "";
      position: absolute;
      left: -10px;
      width: 10px;
      height: 100%;
      box-shadow: 0 0 18px rgba(#9D7F7B, 0.5);
      border-radius: 100%;
      background-color: #eee;
    }
  }
}

.itw-digit__title {
  margin: 0;
  font-size: 32px;
  font-weight: $weight-bold;
}

.itw-digit__text {
  margin-top: -5px;
  margin-bottom: 10px;
  font-size: 16px;
}





@include breakpoint(medium down) {
  .itw-testimonial {
    flex-direction: column;
    padding-left: 2rem;
    padding-right: 2rem;
  }
  .itw-testimonial__quote {
    text-align: center;
  }
  .itw-testimonial__img {
    width: 100%;
  }

  .itw-digits {
    text-align: center;

    .divider-shadow {
      top: 0;
      left: 10%;
      height: 30px;
      width: 80%;
      overflow: hidden;

      &:before {
        content: "";
        position: absolute;
        left: 2%;
        top: -10px;
        width: 96%;
        height: 10px;
        box-shadow: 0 0 18px rgba(#9D7F7B, 0.5);
        border-radius: 100%;
        transition: box-shadow 0.5s;
      }
    }
  }
}


@include breakpoint(small only) {
  .itw-testimonial {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
