
// ==============================================

//        SECTION DON

// ==============================================

.don {
  padding: 80px 0;
  background-image: url('../images/bg-don.jpg');
  background-repeat: no-repeat;
  background-position: right center;
  background-size: contain;
  text-align: center;
  background-color: #FFDAD1;
  // for don choices shadows
  // position: relative;
  // z-index: 1;
}


.don__header {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
}

.don__title {
  font-weight: $weight-bold;
  line-height: 1.3;
  color: $color-primary;
  text-transform: uppercase;

  a {
    color: $color-secondary;
    text-decoration: underline;
    transition: color 0.3s;

    &:hover {
      color: $color-tertiary;
    }
  }
}

.don__subtitle {
  margin-top: 10px;
  margin-bottom: 60px;
  font-size: 26px;
  color: $color-primary;
}


.don__logo-decouvreur {
  position: absolute;
  top: -160px;
  left: -20px;
  height: 160px;
  width: 166px;
}


//  Bloc choices
// -----------------------------------------------

.don__choices {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto 40px;
}

.don__choice {
  position: relative;
  width: 170px;
  margin: 0 1rem 2rem 1rem;
  padding: 15px 5px 0;
  border-radius: 12px;
  color: white;
  background-color: $color-secondary;
  transition: transform 0.3s, background-color 0.3s;
  cursor: pointer;

  &.is-selected {
    background-color: $color-tertiary;

    &:before {
      background-color: $color-tertiary;
      border-color: $color-tertiary;
      transform: translate(-50%, -50%) scale(1.5);
    }

    &:after {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }
  }

  &:hover {
    transform: scale(1.08);
  }

  // bg tick
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 30px;
    height: 30px;
    border: 3px solid $color-secondary;
    border-radius: 50%;
    background-color: white;
    transform: translate(-50%, -50%);
    box-shadow: 0 2px 8px rgba(black,0.3);
    transition: border-color 0.3s, background-color 0.3s, transform 0.3s;
  }

  // tick
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 30px;
    height: 30px;
    background-image: url('../images/icons/icon-check.svg');
    background-repeat: no-repeat;
    background-position: center center;
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5);
    transition: opacity 0.3s 0.1s, transform 0.3s 0.1s;
  }

  p {
    margin-top: -8px;
    margin-bottom: 15px;
    font-size: 19px;
    line-height: 1.4;

    strong {
      font-size: 25px;
    }
  }

  // last choice with input
  &.is-input p {
    font-size: 30px;
    font-weight: $weight-bold;
    line-height: 1;
  }
}

.don__choice__digit {
  display: block;
  font-size: 56px;
  font-weight: $weight-bold;
}

// last choice with input
.don__choice input {
  width: 100%;
  margin-bottom: 5px;
  padding: 8px 8px;
  font-size: 56px;
  font-weight: $weight-bold;
  text-align: center;
  border: none;
  outline-color: transparent;
  outline-style: none;
  color: white;
  background: none;

  &::placeholder {  color: white;  }
}

.don .btn-donate {
  transform: scale(1.15);
}


//  MQ
// -----------------------------------------------

@media screen and (max-width: 830px) {
  .don {
    background-image: none;
  }

  .don__logo-decouvreur {
    top: -180px;
    left: 10px;
  }
  .don__choices {
    max-width: 500px;
  }
}

@include breakpoint(small only) {
  .don__title {
    font-size: 28px;
  }
  .don__subtitle {
    font-size: 22px;
    margin-top: -10px;
  }
  .don__choice__digit {
    font-size: 52px;
  }
  .don__choices {
    margin-bottom: 50px;
  }
  .don__choice {
    margin: 0 0.5rem 1rem 0.5rem;
  }
}




