// ==============================================

//        BUTTONS

// ==============================================


//  Buttons
// -----------------------------------------------
.btn-donate {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  padding: 0 16px;
  font-size: 23px;
  font-weight: $weight-bold;
  border-radius: 30px;
  color: #FFFFFF;
  background-color: $color-secondary;
  transition: background-color 0.3s, opacity 0.3s;
  cursor: pointer;

  span {
    margin-right: 15px;
    margin-left: 15px;
  }

  .icon__round-wrapper {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: $color-secondary-light;
  }

  .icon {
    height: 0.7em;
    margin-left: 2px;
    fill: white;
    transition: transform 0.3s;
  }

  &:hover {
    background-color: $color-secondary-light;

    .icon {
      transform: translateX(4px);
    }
  }

  &:disabled {
    pointer-events:none;
    opacity: 0.5;
  }
}


//  Buttons social
// -----------------------------------------------

.btn-social {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  width: 36px;
  font-size: 25px;    // set svg size (svg height = font-size)

  &:hover .icon {
    fill: $color-secondary;
  }

  .icon {
    fill: white;
    transition: fill 0.3s;
  }
}
