// ==============================================

//        SLICK THEME CUSTOM

// ==============================================

$slick-arrow-width  : 64px;
$slick-arrow-height : 50px;
$slick-dot-size     : 10px;
$slick-dot-margin   : 3px;


//  Arrows
// -----------------------------------------------
.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;
  display: block;
  width: $slick-arrow-width;
  height: $slick-arrow-height;
  transform: translate(0, -50%);
  color: transparent;    // hide "next / prev" labels
  background: transparent;
  outline: none;
  cursor: pointer;
}

.slick-prev {
  left: - $slick-arrow-width - 5px;

  // arrows inside slider
  .slider-equipe-members & {
    @include breakpoint(medium down) {
      left: calc(50% - 160px);
      z-index: 1;    // !!! special : bring in front of the member vegetable
    }
  }
}

.slick-next {
  right: -$slick-arrow-width - 5px;

  // arrows inside slider
  .slider-equipe-members & {
    @include breakpoint(medium down) {
      left: calc(50% + 95px);
    }
  }
}

.slick-prev:before,
.slick-next:before {
  content: '';
  display: block;
  height: 100%;
  margin: 0 auto;
  background-image: url('../images/icons/icon-arrow.svg');
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.75;
  transition: opacity 0.3s, transform 0.3s;
  // @include bd(black, 0.5);
}

.slick-prev:before {
  transform: scaleX(-1);
}

.slick-prev:hover:before,
.slick-prev:focus:before {
  opacity: 1;
  transform: translateX(-10px) scaleX(-1);
}
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
  transform: translateX(10px);
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  // opacity: 0.35;
  display: none;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  pointer-events: none;
}


//  Dots
// -----------------------------------------------

.slick-dots {
  position: absolute;
  bottom: 0;
  left: 50%;
  display: block;
  margin: 0;
  padding: 10px;
  // width: 100%;
  list-style: none;
  text-align: center;
  font-size: 0;
  transform: translateX(-50%);
  z-index: 1;      // !!! special : bring in front of the parallax vegetable

  @include breakpoint(medium down) {
    padding: 20px;
  }

  // dot item
  li {
    position: relative;
    display: inline-block;
    height: $slick-dot-size;
    width: $slick-dot-size;
    margin: 0 $slick-dot-margin;
    padding: 0;

    @include breakpoint(medium down) {
      height: 20px;
      width: 20px;
      margin: 0 6px;
      // @include bg();
    }

    button {
      display: block;
      height: 100%;
      width: 100%;
      padding: 0;
      border: 0;
      font-size: 0;
      line-height: 0;
      color: transparent;
      background: transparent;
      outline: none;
      cursor: pointer;

      &:hover:before, &:focus:before {
        background-color: rgba(white, 0.6);
      }

      // dot content
      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid white;
        background-color: rgba(white, 0);
        opacity: 1;
        transform: translate(-50%, -50%);
        transition: background-color 0.3s;

        @include breakpoint(medium down) {
          height: 80%;
          width: 80%;
        }
      }
    }

    &.slick-active button:before {
      background-color: rgba(white, 1);
    }
  }
}



