// ==============================================

//        BOX DON

// ==============================================


//  Box close
// -----------------------------------------------

.box-don {
  width: 230px;
  font-size: 23px;
  font-weight: $weight-bold;
  text-align: center;
  border-radius: 30px;
  background-color: $color-secondary;
  transition: background-color 0.3s, opacity 0.3s, box-shadow 0.3s, height 0.3s;
  height: 60px;
  overflow: hidden;

  header {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    padding: 0 16px;
  }

  header span {
    color: #FFFFFF;
    margin-right: 15px;
    margin-left: 15px;
    transition: color 0.3s
  }

  header .icon__round-wrapper {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: $color-secondary-light;
    transition: transform 0.3s;
  }

  header .icon {
    height: 0.7em;
    margin-left: 0;
    margin-top: 4px;
    fill: white;
    transform: rotate(90deg);
    transition: transform 0.3s;
  }

  footer {
    // height: 0;
    visibility: hidden;
    opacity: 0;
    transform: translateY(30px);
    transition: visibility 0.3s, opacity 0.3s, transform 0.3s;
  }

  .box-don__choices {
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
    // @include bd();
  }

  .box-don__choice {
    padding: 10px;
    margin-bottom: 10px;
    font-size: 23px;
    font-weight: $weight-bold;
    border-radius: 10px;
    color: white;
    background-color: $color-secondary;
    transition: background-color 0.3s;
    cursor: pointer;

    // &:not(.is-input):not(.is-selected):hover {
    //   background-color: darken($color-secondary,10);
    // }

    &.is-selected:not(.is-input) {
      background-color: $color-tertiary;
      //
      // input {
      //   color: white;
      // }
    }
  }

  .is-input {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
    background-color: white;
    text-align: left;
    cursor: auto;

    input {
      width: 80%;
      font-size: 20px;
      border: none;
      outline-color: transparent;
      outline-style: none;
      color: $color-body-text;
      background: none;

      &::placeholder {  color: $color-body-text;  }
    }

    &:after {
      content: '€';
      display: inline-block;
      position: absolute;
      top: 50%;
      right: 15px;
      font-size: 20px;
      color: $color-body-text;
      transform: translateY(-50%);
    }
  }

  .box-don__btn-donate {
    @extend .btn-donate;
    margin-bottom: 20px;
  }
}


//  Box open
// -----------------------------------------------

.box-don:hover {
  background-color: #FCF9F8;
  box-shadow: 0 10px 14px 0 rgba(black,0.3);
  height: 278px;    // hard coded for transition !!!!

  header span {
    color: $color-secondary;
  }

  header .icon__round-wrapper {
    background-color: #FFDAD1;
  }

  header .icon {
    fill: $color-secondary;
    margin-top: 0;
    transform: rotate(-90deg);
  }

  footer {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
    transition: visibility 0.3s, opacity 0.6s 0.1s, transform 0.3s;
  }
}


