//  Shadow lines
// -----------------------------------------------

@mixin divider-shadow($position:'top', $color:#000000, $alpha:0.4) {
  height: 30px;
  overflow: hidden;
  // @include bd();

  &:before {
    content: "";
    position: absolute;
    left: 2%;
    width: 96%;
    height: 10px;
    box-shadow: 0 0 18px rgba($color, $alpha);
    border-radius: 100%;
    transition: box-shadow 0.5s;
  }

  @if ($position == 'top') {
    &:before {
      top: -10px;
    }
  }
  @if ($position == 'bottom') {
    &:before {
      bottom: -10px;
    }
  }
}


//  Utils
// -----------------------------------------------

// horizontal list
@mixin list-inline() {
  @include list();

  > li {
    display:inline-block;
  }
}


// list without bullets
@mixin list() {
  list-style: none;
  padding-left: 0;
}


// clearfix for floats
@mixin clearfix {
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
  }
}


// Debug with background
@mixin bg($color:#ff0000, $opacity:0.1) {
  background-color: rgba($color, $opacity);
}

// Debug with border
@mixin bd($color:#000000, $opacity:0.15, $thickness:1) {
  outline: #{$thickness}px dotted rgba($color, $opacity);
}



// no gutter for columns >>> to apply on column NOT row
@mixin collapse-left() {
  padding-left: 0;
  // padding-right: map-get($grid-column-gutter, medium)/2;
}
@mixin collapse-right() {
  // padding-left: map-get($grid-column-gutter, medium)/2;
  padding-right: 0;
}
@mixin collapse-all() {
  padding-left: 0;
  padding-right: 0;
}
@mixin collapse-middle() {
  padding-left: 0.9375rem / 2;
  padding-right: 0.9375rem / 2;
}


// Set gutter on column
@mixin column-gutter($left:null, $right:null) {
  @if ($left) {
    padding-left: $left;
  }
  @if ($right) {
    padding-right: $right;
  }
}

