/// Create burger menu button with icon
/// @param {Number} $icon-width — Icon width
/// @param {Number} $icon-height — Icon height
/// @param {Number} $line-height — Symbol bar height
/// @param {Hex} $color — Icon color
/// @param {Float} $transition-time — Animation duration
///
/// @returns {Number}
///
/// @example scss - Usage
/// @include icon-burger($icon-width:18px, $icon-height:16px, $line-height:2px);
///
@mixin icon-burger($icon-width, $icon-height, $line-height, $color:#000000, $transition-time:0.2s) {

  $icon-centerY: ($icon-height - $line-height) / 2;

  // icon wrapper
  position: relative;
  width: $icon-width;
  height: $icon-height;

  // bars
  span {
    position: absolute;
    display: block;
    height: $line-height;
    width: 100%;
    background-color: $color;
    border-radius: $line-height;
    opacity: 1;
    transition: $transition-time ease-in-out;
  }

  // state burger
  span:nth-child(1) {
    top: 0;
    transition-delay: 0.1s;
  }
  span:nth-child(2) {
    top: $icon-centerY;
    transition-delay: 0;
  }
  span:nth-child(3) {
    top: $icon-centerY;
    transition-delay: 0;
  }
  span:nth-child(4) {
    top: $icon-height - $line-height;
    transition-delay: 0.1s;
  }

  // state close
  &.is-active {
    span:nth-child(1) {
      transform: translateY($icon-centerY);
      transition-delay: 0s;
      opacity: 0;
    }
    span:nth-child(2) {
      transform: rotate(45deg);
      transition-delay: 0.1s;
    }
    span:nth-child(3) {
      transform: rotate(-45deg);
      transition-delay: 0.1s;
    }
    span:nth-child(4) {
      transform: translateY(-$icon-centerY);
      transition-delay: 0s;
      opacity: 0;
    }
  }
}



//  Custom
// -----------------------------------------------


// full overlay block in absolute position
@mixin overlay {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

// green overlay on hover on blog image, gallery image...
@mixin overlay-hover {
  // overlay
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: linear-gradient(to right bottom, rgba(white,0.3), rgba($color-primary,0.6));
    opacity: 0;
    transition: opacity 0.4s;
  }

  &:hover:after {
    opacity: 1;
  }
}


// animate underline on hover
@mixin link-underline() {

  &:hover:after {
    transform-origin: left;
    transform: scaleX(1);
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #e1e1e1;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.3s;
    transition-timing-function: cubic-bezier(0,.98,.64,.92);
  }
}


