              @mixin link-underline-post($target, $rev:false) {
                #{$target} {
                  padding-bottom: 1px;
                  background: linear-gradient(to right, $color-primary, $color-link);
                  background-size: 0 1px;
                  background-repeat: no-repeat;
                  background-position: right 85%;
                  transition: background-size 0.3s;
                  transition-timing-function: cubic-bezier(0,.98,.64,.92);
                  @if ($rev) {
                    background-position: left 85%;
                  }
                }

                &:hover #{$target} {
                  background-size: 100% 1px;
                  background-position: left 85%;
                  @if ($rev) {
                    background-position: right 85%;
                  }
                }
              }
