
// ==============================================

//        HEADER

// ==============================================


//  Header
// -----------------------------------------------

.header {
  background-color: white;

  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 110px;
  }

  .logo {
    width: 86px;
    height: 99px;

    svg {
      width: 86px;
      height: 99px;
    }
  }

  .logo-mobile {
    display: none;
    width: 181px;
    height: 30px;
    margin-left: 1rem;

    svg {
      width: 181px;
      height: 30px;
    }
  }

  .box-don {
    position: absolute;
    right: 1rem;
    top: 25px;
    z-index: 1;
  }

  .btn-donate--mobile {
    display: none;
    position: absolute;
    right: 1rem;
    top: 13px;
    transform-origin: top right;
    transform: scale(0.6);
  }
}



//  MQ
// -----------------------------------------------

@include breakpoint(small down) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    z-index: 10;

    .wrapper {
      justify-content: left;
      height: 60px;
    }
    .logo {
      display: none;
    }
    .logo-mobile {
      display: block;
    }
    .btn-donate--mobile {
      display: inline-flex;
    }
    .box-don {
      display: none;
    }
  }
}
