// ==============================================

//        FUNCTIONS

// ==============================================

// Function for converting a px based font-size to rem.
@function px-to-rem ($target) {
  @return $target / 16px * 1rem;
}

// Set the height of an element by calculating its width
// Grid of 2 : $wrapper-padding -- [item] -- gutter -- [item] -- $wrapper-padding
// Grid of 1 : $wrapper-padding -- [item] -- $wrapper-padding
@function setHeightByRatio ($ratio, $column) {
  $gutter: 0px;    // no gutter if 1 column
  @if ($column == 2) {
    $gutter: #{map-get($grid-column-gutter, medium)};
  }

  $width: calc((100vw - #{$wrapper-padding}*2 - #{$gutter} ) / #{$column});
  $height: calc(#{$width} / #{$ratio});

  @return $height;
}



// A map of z layers
// All z-index values should be set there

$z-layers: (
  'modal'         : 100,
  'overlay'       : 90,
  'dropdown'      : 50,
  'header'        : 10,
  'donate-bar'    : 9,
  'nav-overlay'   : 9,
  'default'       : 1,
  'below'         : -1,
  'bottomless-pit': -10000
);

// Set z-index
@function z($layer) {
  @if not map-has-key($z-layers, $layer) {
    @warn "No z-index found in $z-layers map for `#{$layer}`. Property omitted.";
  }

  @return map-get($z-layers, $layer);
}
